<template>
  <div class="table">
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>
          <i class="el-icon-tickets"></i> Song List Information
        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <div class="handle-box">
        <el-button
          type="primary"
          size="mini"
          class="handle-del mr10"
          @click="delAll"
          >Batch Delete</el-button
        >
        <el-input
          v-model="select_word"
          size="mini"
          placeholder="Select keywords"
          class="handle-input mr10"
        ></el-input>
        <el-button
          type="primary"
          size="mini"
          @click="centerDialogVisible = true"
          >Add Song</el-button
        >
      </div>
      <el-table
        :data="tableData"
        border
        size="mini"
        style="width: 100%"
        ref="multipleTable"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="50"></el-table-column>
        <el-table-column prop="name" label="Singer-歌曲"></el-table-column>
        <el-table-column label="Operation" width="80">
          <template slot-scope="scope">
            <el-button
              size="small"
              type="danger"
              @click="handleDelete(scope.row.id)"
              >Delete</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>

    <el-dialog
      title="Add Song"
      :visible.sync="centerDialogVisible"
      width="400px"
      center
    >
      <el-form
        :model="registerForm"
        status-icon
        ref="registerForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item prop="singerName" label="Singer Name" size="mini">
          <el-input
            v-model="registerForm.singerName"
            placeholder="Singer Name"
          ></el-input>
        </el-form-item>
        <el-form-item prop="songName" label="Song" size="mini">
          <el-input
            v-model="registerForm.songName"
            placeholder="Song"
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="centerDialogVisible = false"
          >Cancel</el-button
        >
        <el-button type="primary" size="mini" @click="getSongId">OK</el-button>
      </span>
    </el-dialog>

    <el-dialog title="Hint" :visible.sync="delVisible" width="300px" center>
      <div class="del-dialog-cnt" align="center">
        Deletion cannot be recovered. Are you sure to delete it?
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="delVisible = false">Cancel</el-button>
        <el-button type="primary" size="mini" @click="deleteRow">OK</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mixin } from "../mixins";
import { HttpManager } from "../api/index";

export default {
  name: "list-song-page",
  mixins: [mixin],
  data() {
    return {
      registerForm: {
        singerName: "",
        songName: ""
      },
      tableData: [],
      tempDate: [],
      multipleSelection: [],
      centerDialogVisible: false,
      editVisible: false,
      delVisible: false,
      select_word: "",
      idx: -1
    };
  },
  watch: {
    select_word: function() {
      if (this.select_word === "") {
        this.tableData = this.tempDate;
      } else {
        this.tableData = [];
        for (let item of this.tempDate) {
          if (item.name.includes(this.select_word)) {
            this.tableData.push(item);
          }
        }
      }
    }
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      this.tableData = [];
      this.tempDate = [];
      HttpManager.getListSongOfSongId(this.$route.query.id)
        .then(res => {
          console.log(res);
          for (let item of res) {
            this.getSong(item.songId);
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    getSong(id) {
      HttpManager.getSongOfId(id)
        .then(res => {
          this.tableData.push(res[0]);
          this.tempDate.push(res[0]);
        })
        .catch(err => {
          console.log(err);
        });
    },
    getSongId() {
      let _this = this;
      var id =
        _this.registerForm.singerName + "-" + _this.registerForm.songName;
      HttpManager.getSongOfSingerName(id).then(res => {
        this.addSong(res[0].id);
      });
    },
    addSong(id) {
      let params = new URLSearchParams();
      params.append("songId", id);
      params.append("songListId", this.$route.query.id);
      HttpManager.setListSong(params)
        .then(res => {
          if (res.code === 1) {
            this.getData();
            this.notify("Successfully added", "success");
          } else {
            this.notify("Failed to add ", "error");
          }
        })
        .catch(err => {
          console.log(err);
        });
      this.centerDialogVisible = false;
    },
    deleteRow() {
      HttpManager.deleteListSong(this.idx)
        .then(res => {
          if (res) {
            this.getData();
            this.notify("Deleted successfully", "success");
          } else {
            this.notify("Failed to delete", "error");
          }
        })
        .catch(err => {
          console.log(err);
        });
      this.delVisible = false;
    }
  }
};
</script>

<style scoped>
.handle-box {
  margin-bottom: 20px;
}

.handle-input {
  width: 300px;
  display: inline-block;
}
</style>



